/* eslint-disable max-len */
import React, { useState } from 'react';
import { useHistory } from 'react-router';
import { Row, Col, Container, CardBody, Card } from 'reactstrap';
import { AvForm, AvField } from 'availity-reactstrap-validation';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';

import { ReactComponent as Logo } from '../../../assets/images/logo.svg';
import loginBg from '../../../assets/images/login-bg2.jpeg';

import { EMAIL_REQUIRED, PASSWORD_REQUIRED } from '../../../constants/errorConstants';
// VALID_EMAIL
import { login } from '../../../store/actions';

import '../auth.css';

const Login = () => {
  const history = useHistory();
  const [showPwd, setShowPwd] = useState(false);
  const dispatch = useDispatch();

  const { isLoading } = useSelector(state => state.auth);
  // handleValidSubmit
  const handleValidSubmit = (event, values) => {
    dispatch(login({ values, history }));
  };

  return (
    <>
      <div className="login">
        <img src={loginBg} alt="Login Background" className="login-bg" />
        <Container className="top-spacing">
          <Row className="align-items-center justify-content-center">
            <Col md={8} lg={6} xl={5}>
              <Card>
                <CardBody className="p-4">
                  <Row>
                    <Col lg={12}>
                      <div className="text-center">
                        <Logo />
                      </div>
                    </Col>
                  </Row>
                  <Row className="text-center mt-3">
                    <span className="header-text">Hardisty Crude Classification Tracker</span>
                  </Row>
                  <div className="p-2 mt-4">
                    <AvForm
                      className="form-horizontal"
                      onValidSubmit={(e, v) => {
                        handleValidSubmit(e, v);
                      }}
                    >
                      <div className="mb-3">
                        <AvField
                          name="username"
                          label="Username"
                          value=""
                          className="form-control"
                          placeholder="Enter Username"
                          type="text"
                          required
                          errorMessage={EMAIL_REQUIRED}
                          // validate={{
                          //   required: { value: true },
                          //   pattern: {
                          //     value:
                          //       // eslint-disable-next-line no-useless-escape
                          //       /^(([^<>()[\]\.,;:\s@\"]+(\.[^<>()[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i,
                          //     errorMessage: VALID_EMAIL,
                          //   },
                          // }}
                        />
                      </div>
                      <div className="rel mb-3">
                        {/* <ActivatePassword /> */}
                        <AvField
                          name="password"
                          label="Password"
                          value=""
                          type={showPwd ? 'text' : 'password'}
                          errorMessage={PASSWORD_REQUIRED}
                          validate={{ required: { value: true } }}
                          placeholder="Enter Password"
                        />
                        {showPwd ? (
                          <i
                            className="show_pwd fa-regular fa-eye"
                            role="button"
                            aria-label="Show Password"
                            tabIndex={0}
                            onKeyDown={() => setShowPwd(false)}
                            onClick={() => setShowPwd(false)}
                          />
                        ) : (
                          <i
                            className="hide_pwd fa-solid fa-eye-slash"
                            role="button"
                            aria-label="Hide Password"
                            tabIndex={0}
                            onKeyDown={() => setShowPwd(true)}
                            onClick={() => setShowPwd(true)}
                          />
                        )}
                      </div>
                      {/* <div className="mb-3 d-flex">
                        <AvField type="checkbox" name="remember" value="Remember" />
                        <span className="mx-2">Remember Me</span>
                      </div> */}
                      {/* {error && <p className="error">{error}</p>} */}
                      <div className="text-end">
                        <Link to="/login" className="text-semi-bold text-red link-text">
                          Forgot password?
                        </Link>
                      </div>
                      <div>
                        <button
                          className="btn btn-danger w-100 waves-effect waves-light mt-3"
                          type="submit"
                        >
                          {isLoading ? (
                            <i className="fa-solid fa-circle-notch fa-spin" />
                          ) : (
                            'Log In'
                          )}
                        </button>
                      </div>
                    </AvForm>
                  </div>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </Container>
      </div>
    </>
  );
};

export default Login;
